              (!$store.getters.dataLoaded && this.checkIsError.error) ||
              ($store.getters.dataLoaded &&
                !$store.getters.isLoading &&
                this.$store.getters.loadData['counter'] != '')
            "
          >
            <dividend-output
              :data="FilteredResult"
              :error="this.checkIsError.error"
            ></dividend-output>
