<template>
  <div>
    <div class="row" v-if="this.data.Status === 200">
      <div class="col-12 col-lg-6 col-xl-4">
        <a
          style="text-decoration: none"
          href="https://www.buymeacoffee.com/savajava/membership"
          target="_blank"
        >
          <button class="btn donate-btn w-100 mb-3">
            <img src="/coffee-cup.png" alt="buy me coffee" />
            <strong> Buy me a coffee </strong>
          </button>
        </a>
        <button
          class="w-100 btn btn-secondary back-button mb-3"
          @click="openCalc"
          v-if="this.dividends"
        >
          <i class="bi bi-calculator-fill"></i> Dividend Projection Calculator
        </button>
        <button
          class="w-100 btn btn-secondary back-button mb-5"
          @click="backToMain"
        >
          <i class="bi bi-arrow-left"></i> Back
        </button>
      </div>
      <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
      <div class="col-12 mb-3">
        <div
          class="d-block d-lg-flex justify-content-center justify-content-lg-start align-items-center mb-3"
        >
          <div class="img-wrapper mb-3 mb-lg-0 mx-lg-0 me-lg-3">
            <img
              class="logo"
              :src="logo"
              onerror="javascript:this.src='https://www.freeiconspng.com/uploads/no-image-icon-4.png'"
            />
          </div>
          <h2 class="text-center text-lg-start">
            <span class="title">
              <strong>
                {{ this.data["Counter"].replaceAll(",", "") }}
              </strong>
            </span>
          </h2>
        </div>
      </div>
      <div class="col-12 col-lg-6 col-xxl-4" v-if="this.dividends">
        <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
        <h3 class="text-center text-lg-start my-3">Dividend Analysis</h3>
        <div class="table-wrapper">
          <table class="table table-hover">
            <tr v-if="isYearsMissing">
              <th>Dividend Paying Frequency</th>
              <td>
                {{
                  frequency === "inconsistent" ? frequencyFallback : frequency
                }}
              </td>
            </tr>
            <tr v-if="isYearsMissing">
              <th>Forward Annual Dividend Yield</th>
              <td>{{ this.divYield }}</td>
            </tr>
            <tr v-if="isYearsMissing">
              <th>Forward Average Annual Yield</th>
              <td>
                {{
                  setAvgYield(
                    this.data["Dividend Analysis"][
                      "All-Time Average Dividend per Payout"
                    ]
                  )
                }}
              </td>
            </tr>
            <tr>
              <th>
                All-Time Average Yield -
                {{
                  frequency === "inconsistent" ? frequencyFallback : frequency
                }}
              </th>
              <td>
                {{
                  this.data["Dividend Analysis"][
                    "All-Time Average Yield Based on Current price"
                  ]
                }}
              </td>
            </tr>
            <tr>
              <th>
                All-Time Average Dividend -
                {{
                  frequency === "inconsistent" ? frequencyFallback : frequency
                }}
              </th>
              <td>
                {{
                  this.data["Dividend Analysis"][
                    "All-Time Average Dividend per Payout"
                  ]
                }}
              </td>
            </tr>
            <tr>
              <th>Total Dividend Payment Count</th>
              <td>
                {{
                  this.data["Dividend Analysis"]["Total Dividend Payment Count"]
                }}
              </td>
            </tr>
            <tr v-if="!isYearsMissing">
              <th>Last Dividend Paid</th>
              <td>
                {{
                  this.data["Dividend Analysis"][
                    "Last Dividend Paid since(years)"
                  ].toFixed(1)
                }}
                years ago
              </td>
            </tr>
            <tr>
              <th>Current Dividend Paying Status</th>
              <td :class="{ red: !divPayingStatus, green: divPayingStatus }">
                <div class="d-flex scalable">
                  <img class="star" :src="divPayingStatusPic" />
                  {{ divPayingStatus ? "ACTIVE" : "INACTIVE" }}
                </div>
              </td>
            </tr>
            <tr v-if="isYearsMissing">
              <th>Current Dividend Payment Evaluation</th>
              <td
                :class="{
                  red: !this.isValueAvgDivYield,
                  green: this.isValueAvgDivYield,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="this.avgDivVsFwdYieldPic" />
                  <span class="nowrap">{{
                    this.isValueAvgDivYield ? "ABOVE AVERAGE" : "BELOW AVERAGE"
                  }}</span>
                </div>
              </td>
            </tr>
            <tr
              v-if="
                this.priceToBookValue !== Infinity &&
                this.priceToBookValue !== 'N/A' &&
                !isNaN(this.priceToBookValue)
              "
            >
              <th>Price-to-Book Ratio (PB)</th>
              <td
                :class="{
                  green:
                    this.priceToBookValue > 0 && this.priceToBookValue <= 1,
                  orange:
                    this.priceToBookValue > 1 && this.priceToBookValue <= 10,
                  red: this.priceToBookValue < 0,
                  red2: this.priceToBookValue > 10,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="this.priceToBookValuePic" />
                  {{ this.priceToBookValue.toFixed(2) }}x
                </div>
              </td>
            </tr>
            <tr>
              <th>Price-to-Earning Ratio (PE)</th>
              <td
                :class="{
                  green: this.peRatio < 10,
                  orange: this.peRatio >= 10 && this.peRatio < 20,
                  red: this.peRatio > 20,
                }"
              >
                <div class="d-flex scalable" v-if="this.peRatio !== 'No Data'">
                  <img class="star" :src="peRatioPic" />
                  {{ this.peRatio }}x
                </div>
                <div v-else>
                  {{ this.peRatio }}
                </div>
              </td>
            </tr>
            <tr
              v-if="
                debt !== 'N/A' &&
                income !== 'N/A' &&
                !isNaN(debtToIncomeRatio(this.debt, this.income))
              "
            >
              <th>Debt to Income Ratio</th>
              <td
                :class="{
                  green: debtMode < 1,
                  orange: debtMode >= 1 && debtMode < 1.5,
                  red: debtMode >= 1.5,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="debtModePic" />
                  {{ debtToIncomeRatio(this.debt, this.income) }}x
                </div>
              </td>
            </tr>
            <tr v-if="!isNaN(cashToDebtRatio(this.cash, this.debt))">
              <th>Debt to Cash Ratio</th>
              <td
                :class="{
                  green: cashMode < 1,
                  orange: cashMode >= 1 && cashMode < 1.5,
                  red: cashMode >= 1.5,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="cashStatusPic" />
                  {{ cashToDebtRatio(this.cash, this.debt) }}x
                </div>
              </td>
            </tr>
            <tr>
              <th>Dividend Defaults or Late Payment Strike(s)</th>
              <td
                :class="{
                  green: mode === 0,
                  orange: mode > 0 && mode <= 2,
                  red: mode >= 3,
                }"
              >
                <div class="d-flex scalable">
                  <img v-if="!isNaN(mode)" class="star" :src="star" />
                  {{ mode === 0 ? "CLEAN" : "" }}
                  {{ mode === 1 ? mode + " hit" : "" }}
                  {{ mode >= 2 ? mode + " hits" : "" }}
                  {{ isNaN(mode) ? "N/A" : "" }}
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <hr v-if="this.dividends" />
      <div class="col-12 col-lg-6 col-xxl-4">
        <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
        <h3 class="text-center text-lg-start my-3">Fundamentals</h3>
        <div class="table-wrapper">
          <table class="table table-hover">
            <tr>
              <th>Sector</th>
              <td>
                {{
                  this.data.Fundamentals.Sector !== "" &&
                  this.data.Fundamentals.Sector !== "N/A" &&
                  this.data.Fundamentals.Sector !== null
                    ? this.data.Fundamentals.Sector
                    : "No Data"
                }}
              </td>
            </tr>
            <tr>
              <th>Current Price</th>
              <td>
                {{ dataValidityChecker(this.price, this.price) }}
              </td>
            </tr>
            <tr>
              <th>Market Cap</th>
              <td>
                {{
                  intToAlphaConverter(
                    this.data.Fundamentals["Total Shares Outstanding"] *
                      this.data["Last Done Price"]
                  ) !== ""
                    ? intToAlphaConverter(
                        this.data.Fundamentals["Total Shares Outstanding"] *
                          this.data["Last Done Price"]
                      )
                    : "No Data"
                }}
              </td>
            </tr>
            <tr>
              <th>Shares Outstanding</th>
              <td>
                {{
                  dataValidityChecker(
                    this.data.Fundamentals["Total Shares Outstanding"],
                    intToAlphaConverter(
                      this.data.Fundamentals["Total Shares Outstanding"]
                    )
                  )
                }}
              </td>
            </tr>
            <tr>
              <th>Total Cash Balance</th>
              <td>
                {{
                  this.data.Fundamentals["Total Cash Balance (mrq)"] !== null
                    ? intToAlphaConverter(
                        alphaToIntConverter(
                          this.data.Fundamentals["Total Cash Balance (mrq)"]
                        )
                      )
                    : "No Data"
                }}
              </td>
            </tr>
            <tr>
              <th>Book Value</th>
              <td>
                {{
                  dataValidityChecker(
                    this.data.Fundamentals["Book Value"],
                    intToAlphaConverter(this.data.Fundamentals["Book Value"])
                  )
                }}
              </td>
            </tr>
            <tr>
              <th>Net Income</th>
              <td>
                {{
                  dataValidityChecker(
                    this.data.Fundamentals["Net Income (ttm)"],
                    intToAlphaConverter(
                      this.data.Fundamentals["Net Income (ttm)"]
                    )
                  )
                }}
              </td>
            </tr>
            <tr>
              <th>Total Debt</th>
              <td>
                {{
                  dataValidityChecker(
                    this.data.Fundamentals["Total Debt (mrq)"],
                    intToAlphaConverter(
                      this.data.Fundamentals["Total Debt (mrq)"]
                    )
                  )
                }}
              </td>
            </tr>
            <tr
              v-if="
                !this.dividends &&
                debt !== 'N/A' &&
                income !== 'N/A' &&
                !isNaN(debtToIncomeRatio(this.debt, this.income))
              "
            >
              <th>Debt to Income Ratio</th>
              <td
                :class="{
                  green: debtMode < 1,
                  orange: debtMode >= 1 && debtMode < 1.5,
                  red: debtMode >= 1.5,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="debtModePic" />
                  {{ debtToIncomeRatio(this.debt, this.income) }}x
                </div>
              </td>
            </tr>
            <tr
              v-if="
                !this.dividends && !isNaN(cashToDebtRatio(this.cash, this.debt))
              "
            >
              <th>Debt to Cash Ratio</th>
              <td
                :class="{
                  green: cashMode < 1,
                  orange: cashMode >= 1 && cashMode < 1.5,
                  red: cashMode >= 1.5,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="cashStatusPic" />
                  {{ cashToDebtRatio(this.cash, this.debt) }}x
                </div>
              </td>
            </tr>
            <tr v-if="!this.dividends">
              <th>PE Ratio</th>
              <td
                :class="{
                  green: this.peRatio < 10,
                  orange: this.peRatio >= 10 && this.peRatio < 20,
                  red: this.peRatio > 20,
                }"
              >
                <div class="d-flex scalable" v-if="this.peRatio !== 'No Data'">
                  <img class="star" :src="peRatioPic" />
                  {{ this.peRatio }}x
                </div>
                <div v-else>
                  {{ this.peRatio }}
                </div>
              </td>
            </tr>
            <tr
              v-if="
                !this.dividends &&
                this.priceToBookValue !== Infinity &&
                this.priceToBookValue !== 'N/A' &&
                !isNaN(this.priceToBookValue)
              "
            >
              <th>PB Ratio</th>
              <td
                :class="{
                  green:
                    this.priceToBookValue > 0 && this.priceToBookValue <= 1,
                  orange:
                    this.priceToBookValue > 1 && this.priceToBookValue <= 10,
                  red: this.priceToBookValue < 0,
                  red2: this.priceToBookValue > 10,
                }"
              >
                <div class="d-flex scalable">
                  <img class="star" :src="this.priceToBookValuePic" />
                  {{ this.priceToBookValue.toFixed(2) }}x
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="col-12" v-if="!this.dividends">
        <h4 class="remark text-center text-lg-start my-3">
          {{
            this.data["Counter"].split("(")[1].replaceAll(")", "").toUpperCase()
          }}
          never paid a dividend.
        </h4>
      </div>
      <hr v-if="this.dividends" />
      <div class="col-12 col-lg-6 col-xxl-4" v-if="this.dividends">
        <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
        <h3 class="text-center text-lg-start my-3">Dividend History</h3>
        <div class="table-wrapper vertical">
          <table class="table table-hover table-vertical">
            <thead>
              <tr>
                <th>Date Announcement</th>
                <th>Dividend Amount</th>
              </tr>
            </thead>
            <tbody
              v-for="dividend in this.dividendHistory"
              :key="dividend.id"
              :id="dividend.id"
            >
              <tr>
                <td>{{ dividend["Annoucement Date"] }}</td>
                <td>{{ dividend["Dividend Amount"] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="row" v-else>
      <div class="col-12 col-lg-6 col-xl-4">
        <button
          class="w-100 btn btn-secondary back-button mb-5"
          @click="backToMain"
        >
          <i class="bi bi-arrow-left"></i> Back
        </button>
      </div>
      <div class="col-12">
        <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
        <invalid-stocks
          :data="this.data.counter"
          :error="this.error"
        ></invalid-stocks>
      </div>
    </div>
  </div>
  <transition mode="out-in" name="pop" appear>
    <Modal v-if="this.$store.getters.calcIsOpen">
      <div id="container-4cd63e171d86e488f3d6c02f7afd2416"></div>
      <div class="form-input-group text-center text-lg-start">
        <h5 class="mb-4">
          <strong>
            {{ this.data["Counter"].replaceAll(",", "") }}
          </strong>
        </h5>
      </div>
      <div class="form-input-group">
        <h5 class="mb-3">
          <strong>New Units</strong>
        </h5>
      </div>
      <div class="form-input-group">
        <button
          class="btn btn-secondary back-button btn-blue w-100 mb-3"
          @click="useCurrPrice = !useCurrPrice"
        >
          <i class="bi bi-currency-dollar"></i>&nbsp;Use
          {{ useCurrPrice ? "Custom Price" : "Current Price" }}
        </button>
      </div>
      <transition mode="out-in" name="pop" appear>
        <div class="form-input-group" v-if="!useCurrPrice">
          <input
            v-model.trim="customPrice"
            type="number"
            class="form-control mb-3"
            placeholder="Enter new purchase price.."
            min="0.01"
            step="0.01"
            required
          />
        </div>
      </transition>
      <div class="form-input-group">
        <input
          v-model.trim="newUnitsToBuy"
          type="number"
          class="form-control mb-3"
          placeholder="Total units to be purchased.."
          min="1"
          required
        />
      </div>

      <div class="form-input-group">
        <select class="form-select" v-model.trim="taxPercentage" required>
          <option selected disabled value="">Tax deduction (%)</option>
          <option value="0">0%</option>
          <option value="0.37">37%</option>
          <option value="0.35">35%</option>
          <option value="0.32">32%</option>
          <option value="0.3">30%</option>
          <option value="0.24">24%</option>
          <option value="0.22">22%</option>
          <option value="0.12">12%</option>
          <option value="0.1">10%</option>
        </select>
      </div>
      <div class="form-input-group">
        <hr class="line mt-4 my-3" />
      </div>
      <div class="form-input-group">
        <button
          class="btn btn-secondary back-button btn-blue w-100 mb-3"
          @click="showExistingHolder"
        >
          <i class="bi bi-cash-coin"></i>&nbsp;
          {{ existingHolder ? "New Purchase" : "Existing Holdings" }}
        </button>
      </div>
      <transition mode="out-in" name="pop" appear>
        <div v-if="existingHolder">
          <div class="form-input-group">
            <h5 class="mb-3">
              <strong>Existing Units</strong>
            </h5>
          </div>
          <div class="form-input-group">
            <input
              v-model.trim="existingUnits"
              type="number"
              class="form-control mb-3"
              placeholder="Existing units (if any).."
              min="0"
            />
          </div>
          <div class="form-input-group">
            <input
              v-model.trim="existingPrice"
              type="number"
              class="form-control mb-3"
              placeholder="Existing avg price (if any).."
              step="0.01"
              min="0.00"
            />
          </div>
          <div class="form-input-group">
            <hr class="line mt-4 my-3" />
          </div>
        </div>
      </transition>
      <div class="alert alert-primary">
        <div class="form-input-group">
          <h5
            :class="
              newUnitsToBuy !== null || existingUnits !== null ? 'mb-3' : ''
            "
          >
            <strong>Projections</strong>
          </h5>
        </div>
        <transition mode="out-in" name="pop" appear>
          <div
            class="form-input-group"
            v-if="newUnitsToBuy !== null || existingUnits !== null"
          >
            <p class="mb-3">
              <strong>
                {{
                  frequency === "inconsistent" ? frequencyFallback : frequency
                }}
                Dividend (nett): ${{
                  newUnitsToBuy === null ? 0 : getFlexibleDividend().toFixed(2)
                }}
              </strong>
            </p>
            <p class="mb-3">
              <strong
                >Annual Dividend (nett): ${{
                  newUnitsToBuy === null ? 0 : getAnnualDividend().toFixed(2)
                }}
              </strong>
            </p>
            <p class="mb-3">
              <strong
                >New Purchase Amount: ${{
                  newUnitsToBuy === null
                    ? 0
                    : (
                        (this.useCurrPrice
                          ? this.dataValidityChecker(this.price, this.price)
                          : this.customPrice) * newUnitsToBuy
                      ).toFixed(2)
                }}
              </strong>
            </p>
            <transition mode="out-in" name="pop" appear>
              <div v-if="existingHolder">
                <p class="mb-3">
                  <strong
                    >Total Units:
                    {{
                      existingUnits === null && existingPrice === null
                        ? 0
                        : existingUnits + newUnitsToBuy
                    }}</strong
                  >
                </p>
                <p class="mb-3">
                  <strong
                    >Total Purchase Amount: ${{
                      existingUnits === null && existingPrice === null
                        ? 0
                        : (
                            (this.useCurrPrice
                              ? this.dataValidityChecker(this.price, this.price)
                              : this.customPrice) *
                              newUnitsToBuy +
                            existingPrice * existingUnits
                          ).toFixed(2)
                    }}
                  </strong>
                </p>
                <p class="mb-3">
                  <strong
                    >New Average Price: ${{
                      existingUnits === null && existingPrice === null
                        ? 0
                        : (
                            ((this.useCurrPrice
                              ? this.dataValidityChecker(this.price, this.price)
                              : this.customPrice) *
                              newUnitsToBuy +
                              existingPrice * existingUnits) /
                            (existingUnits + newUnitsToBuy)
                          ).toFixed(2)
                    }}
                  </strong>
                </p>
              </div>
            </transition>
            <p class="mb-3">
              <strong>Yield on Purchase: {{ yieldOnPurchase() }}%p.a. </strong>
            </p>
          </div>
        </transition>
      </div>
    </Modal>
  </transition>
</template>
<script>
import InvalidStocks from "./InvalidStocks.vue";
import Modal from "../components/UI/Modal.vue";
export default {
  props: ["error"],
  components: {
    InvalidStocks,
    Modal,
  },
  data() {
    return {
      data: [],
      logo: "",
      dividends: false,
      isYearsMissing: false,
      dividendHistory: [],
      status: "",
      divYield: "",
      totalDiv: "",
      star: "",
      mode: "",
      divPayingStatus: false,
      divPayingStatusPic: "",
      debtPayingStatusPic: "",
      frequency: "",
      serverResponse: null,
      frequencyValues: {
        Monthly: 12,
        Quarterly: 4,
        "Bi-Annually": 2,
        Annually: 1,
      },
      frequencyFallback: "",
      debt: "",
      debtMode: "",
      income: "",
      cash: "",
      cashMode: "",
      cashStatusPic: "",
      starIcon: "/star.png",
      warningIcon: "/warning.png",
      dangerIcon: "/danger.png",
      avgDivYield: "",
      avgDivVsFwdYieldPic: "",
      isValueAvgDivYield: false,
      priceToBookValue: "",
      priceToBookValuePic: "",
      peRatio: "",
      peRatioPic: "",
      existingHolder: false,
      newUnitsToBuy: null,
      taxPercentage: "",
      existingUnits: null,
      existingPrice: null,
      useCurrPrice: true,
      customPrice: null,
    };
  },
  created() {
    this.getData();
    this.setDivYield();
    this.isError();
  },
  mounted() {
    this.metaData();
  },
  updated() {
    this.isError();
  },
  computed: {
    checkIsError() {
      return this.$store.getters.isError.error;
    },
  },
  methods: {
    yieldOnPurchase() {
      let totalAmount =
        (this.useCurrPrice
          ? this.dataValidityChecker(this.price, this.price)
          : this.customPrice) *
          this.newUnitsToBuy +
        this.existingPrice * this.existingUnits;
      let annualDividend = this.getAnnualDividend();
      return ((annualDividend / totalAmount) * 100).toFixed(2);
    },
    getFlexibleDividend() {
      return (
        (1 - this.taxPercentage) *
        (this.newUnitsToBuy + this.existingUnits) *
        parseFloat(
          this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]
        )
      );
    },
    getAnnualDividend() {
      let netDividend =
        (1 - this.taxPercentage) *
        (this.newUnitsToBuy + this.existingUnits) *
        parseFloat(
          this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]
        );

      switch (this.frequency) {
        case "Quarterly":
          return netDividend * 4;
        case "Monthly":
          return netDividend * 12;
        case "Bi-Annually":
          return netDividend * 2;
        case "Annually":
        default:
          return netDividend;
      }
    },
    showExistingHolder() {
      return (this.existingHolder = !this.existingHolder);
    },
    openCalc() {
      let body = document.querySelector("body");
      this.$store.dispatch("openCalc", true);
      body.classList.toggle("fixed");
    },
    isError() {
      this.serverResponse = this.$store.getters.isError;
    },
    dataValidityChecker(a, b) {
      return a !== null && a !== "" && !isNaN(a) ? b : "No Data";
    },
    intToAlphaConverter(num) {
      if (num === null) return;
      const length = num.toString().length;
      if (length >= 3 && length < 7) {
        return (num / 1000).toFixed(2).toString().replace(".00", "") + "K";
      }
      if (length >= 7 && length < 10) {
        return (num / 1000000).toFixed(2).toString().replace(".00", "") + "M";
      }
      if (length >= 10 && length < 13) {
        return (
          (num / 1000000000).toFixed(2).toString().replace(".00", "") + "B"
        );
      }
      if (length >= 13 && length < 15) {
        return (
          (num / 1000000000000).toFixed(2).toString().replace(".00", "") + "T"
        );
      }
    },
    alphaToIntConverter(item) {
      if (item === null) return;
      const val = item.toUpperCase();
      const value = parseFloat(
        val.replace("K", "").replace("M", "").replace("B", "").replace("T", "")
      );

      if (val.includes("K")) {
        return value * 1000;
      }

      if (val.includes("M")) {
        return value * 1000000;
      }

      if (val.includes("B")) {
        return value * 1000000000;
      }

      if (val.includes("T")) {
        return value * 1000000000000;
      }

      return "N/A";
    },
    debtToIncomeRatio(debt, income) {
      return debt / income < 0 ? "N/A" : (debt / income).toFixed(2);
    },
    cashToDebtRatio(cash, debt) {
      return !isNaN(this.alphaToIntConverter(cash)) && !isNaN(debt)
        ? (debt / this.alphaToIntConverter(cash)).toFixed(2)
        : "N/A";
    },
    numberFormatter(num) {
      return new Intl.NumberFormat().format(num);
    },
    getData() {
      this.data = this.$store.getters.loadData;
      if (this.data.Status === 200) {
        this.dividends = this.data["Stock Type"] === "Dividend";
        this.logo = this.$store.getters.loadData["Company Logo"];
        this.debt =
          this.data.Fundamentals["Total Debt (mrq)"] === null
            ? "N/A"
            : this.data.Fundamentals["Total Debt (mrq)"];
        this.sharesOutstanding =
          this.data.Fundamentals["Total Shares Outstanding"] === null
            ? "N/A"
            : this.data.Fundamentals["Total Shares Outstanding"];
        this.income =
          this.data.Fundamentals["Net Income (ttm)"] === null
            ? "N/A"
            : this.data.Fundamentals["Net Income (ttm)"];
        this.cash =
          this.data.Fundamentals["Total Cash Balance (mrq)"] === null
            ? "N/A"
            : this.data.Fundamentals["Total Cash Balance (mrq)"];
        this.price =
          this.data["Last Done Price"] === null
            ? null
            : this.data["Last Done Price"].toFixed(2);

        this.priceToBookValue =
          this.data["Last Done Price"] /
            (this.data.Fundamentals["Book Value"] / this.sharesOutstanding) <
          0
            ? "N/A"
            : this.data["Last Done Price"] /
              (this.data.Fundamentals["Book Value"] / this.sharesOutstanding);
        this.peRatio =
          this.data.Fundamentals["Trailing PE"] !== "" &&
          this.data.Fundamentals["Trailing PE"] !== "N/A" &&
          this.data.Fundamentals["Trailing PE"] !== null
            ? this.data.Fundamentals["Trailing PE"]
            : "No Data";

        this.debtMode = this.debtToIncomeRatio(this.debt, this.income);
        this.cashMode = this.cashToDebtRatio(this.cash, this.debt);

        if (this.peRatio < 10) {
          this.peRatioPic = this.starIcon;
        } else if (this.peRatio >= 10 && this.peRatio < 20) {
          this.peRatioPic = this.warningIcon;
        } else {
          this.peRatioPic = this.dangerIcon;
        }

        if (this.priceToBookValue > 0 && this.priceToBookValue <= 1) {
          this.priceToBookValuePic = this.starIcon;
        } else if (this.priceToBookValue > 1 && this.priceToBookValue <= 10) {
          this.priceToBookValuePic = this.warningIcon;
        } else {
          this.priceToBookValuePic = this.dangerIcon;
        }

        if (this.cashMode < 1) {
          this.cashStatusPic = this.starIcon;
        } else if (this.cashMode >= 1 && this.cashMode < 1.5) {
          this.cashStatusPic = this.warningIcon;
        } else {
          this.cashStatusPic = this.dangerIcon;
        }

        if (this.debtMode < 1) {
          this.debtModePic = this.starIcon;
        } else if (this.debtMode >= 1 && this.debtMode < 1.5) {
          this.debtModePic = this.warningIcon;
        } else {
          this.debtModePic = this.dangerIcon;
        }
      }

      if (this.data.Status === 200 && this.data["Stock Type"] === "Dividend") {
        this.dividendHistory = this.data["Dividend History"];
        this.mode = +this.data["Dividend Suspension Count"];
        this.divPayingStatus =
          this.data["Dividend Analysis"]["Actively Paying"];
        this.frequency = this.data["Dividend Frequency"].trim();
        this.avgDivYield = this.setAvgYield(
          this.data["Dividend Analysis"]["All-Time Average Dividend per Payout"]
        );
        this.isValueAvgDivYield =
          +this.setDivYield().replace("%", "") >
          +this.setAvgYield(
            this.data["Dividend Analysis"][
              "All-Time Average Dividend per Payout"
            ]
          ).replace("%", "");
        this.isYearsMissing =
          this.$store.getters.loadData["Dividend Analysis"]["Actively Paying"];

        if (this.isValueAvgDivYield) {
          this.avgDivVsFwdYieldPic = this.starIcon;
        } else {
          this.avgDivVsFwdYieldPic = this.dangerIcon;
        }

        if (this.mode === 0) {
          this.star = this.starIcon;
        } else if (this.mode > 0 && this.mode <= 2) {
          this.star = this.warningIcon;
        } else {
          this.star = this.dangerIcon;
        }

        if (this.divPayingStatus) {
          return (this.divPayingStatusPic = this.starIcon);
        } else {
          return (this.divPayingStatusPic = this.dangerIcon);
        }
      }

      if (this.data.Status === 404) {
        this.status = this.data.status;
      }
    },
    setAvgYield(val) {
      if (this.$store.getters.loadData["Dividend Frequency"] === "Monthly") {
        this.totalAvgDiv = parseFloat(val) * 12;
      }
      if (this.$store.getters.loadData["Dividend Frequency"] === "Quarterly") {
        this.totalAvgDiv = parseFloat(val) * 4;
      }
      if (
        this.$store.getters.loadData["Dividend Frequency"] === "Bi-Annually"
      ) {
        this.totalAvgDiv = parseFloat(val) * 2;
      }
      if (this.$store.getters.loadData["Dividend Frequency"] === "Annually") {
        this.totalAvgDiv = parseFloat(val) * 1;
      }

      return (this.divAvgYield =
        (
          (this.totalAvgDiv / this.$store.getters.loadData["Last Done Price"]) *
          100
        ).toFixed(2) + "%");
    },
    setDivYield() {
      if (this.$store.getters.loadData["Dividend Frequency"] === "Monthly") {
        this.totalDiv =
          parseFloat(
            this.$store.getters.loadData["Dividend History"][0][
              "Dividend Amount"
            ]
          ) * 12;
      }
      if (this.$store.getters.loadData["Dividend Frequency"] === "Quarterly") {
        this.totalDiv =
          parseFloat(
            this.$store.getters.loadData["Dividend History"][0][
              "Dividend Amount"
            ]
          ) * 4;
      }
      if (
        this.$store.getters.loadData["Dividend Frequency"] === "Bi-Annually"
      ) {
        this.totalDiv =
          parseFloat(
            this.$store.getters.loadData["Dividend History"][0][
              "Dividend Amount"
            ]
          ) * 2;
      }
      if (this.$store.getters.loadData["Dividend Frequency"] === "Annually") {
        this.totalDiv =
          parseFloat(
            this.$store.getters.loadData["Dividend History"][0][
              "Dividend Amount"
            ]
          ) * 1;
      }
      if (
        this.$store.getters.loadData["Dividend Frequency"] === "Inconsistent"
      ) {
        const differenceInMonths =
          (new Date(
            this.$store.getters.loadData["Dividend History"][0][
              "Annoucement Date"
            ]
          ) -
            new Date(
              this.$store.getters.loadData["Dividend History"][1][
                "Annoucement Date"
              ]
            )) /
          (1000 * 60 * 60 * 24 * 7 * 4);

        const frequencyFinal = function () {
          if (differenceInMonths) {
            if (differenceInMonths > 2 && differenceInMonths < 6) {
              return "Quarterly";
            }
            if (differenceInMonths === 2) {
              return "Bi-Annually";
            }
            if (differenceInMonths === 12) {
              return "Monthly";
            }
            if (differenceInMonths === 1) {
              return "Annually";
            }
            return "Inconsistent";
          }
        };

        this.frequencyFallback = frequencyFinal();

        this.totalDiv =
          parseFloat(
            this.$store.getters.loadData["Dividend History"][0][
              "Dividend Amount"
            ]
          ) * this.frequencyValues[frequencyFinal()];
      }
      return (this.divYield =
        (
          (this.totalDiv / this.$store.getters.loadData["Last Done Price"]) *
          100
        ).toFixed(2) + "%");
    },
    backToMain() {
      this.data = [];
      this.$store.commit("loadData", []);
      this.$store.dispatch("isHomePage", true);

      // Resetting header/metadata tags
      document.getElementsByTagName("meta")["description"].content =
        "Dividend stock's credit score at your fingertips";
      document.querySelector("link[rel=icon]").href = "/favicon.png";
      document.title = `SavaJava Dividend Stocks Information and Analysis Screener`;

      if (this.error) {
        this.$store.dispatch("isError", {
          error: false,
          status: null,
        });
        return this.$store.dispatch("dataLoaded", false);
      }
      return this.$store.dispatch("dataLoaded", false);
    },
    metaData() {
      document.getElementsByTagName("meta")["description"].content =
        this.$store.getters.loadData.Status === 200
          ? `Showing dividend details of ${this.data["Counter"].replaceAll(
              ",",
              ""
            )}`
          : `Invalid search parameter detected, as no record found for ${this.data.counter}!`;
      document.querySelector("link[rel=icon]").href =
        this.$store.getters.loadData.Status === 200
          ? this.logo
          : "/favicon.png";
      document.title =
        this.$store.getters.loadData.Status === 200
          ? `Search Result for ${this.data["Counter"].replaceAll(",", "")}`
          : `Search for ${this.data.counter} not found!`;
    },
  },
};
</script>
